@function twbs-font-path($path, $name) {
  @return "";
}

//padding
@function get-btn-padding($height, $font-size, $line-height, $border:1px){
  @warn "#{ round(($height - $font-size * $line-height - $border*2)/2)},#{$height}, #{$font-size}, #{$line-height}, #{$border}";
  @return round(($height - $font-size * $line-height - $border*2)/2);
}

// input button
@function get-height($height, $padding-top, $border){
  @return $height - ($padding-top + $border)*2
}

//line-height
@function get-line-height($height, $border:1px){
  @return $height - $border*2;
}